<template lang="pug">
    .tests__content
        .tests__header
            h4.tests__title Пройденные тесты
            .tests__filter
                label.label__filter(for='subjects') Выберите предмет:
                ul.subjects__list
                    select(v-model="selectedSubjectId").tests__filter-select
                        option.subject__option(:value='0') Все
                        option.subject__option(:value='item.id' v-for='item in subjects') {{item.name}}             
        ul.tests__list
            li.tests__item.names
                .tests__number # 
                .tests__code Код теста
                .tests__subject Предмет
                .tests__class Класс
                .tests__results Результат
            li.tests__item(v-for="item,idx in filteredTests" :key="item.id")
                .tests__number {{idx+1}}
                .tests__code {{item.test.code}}
                .tests__subject {{item.test.group.subject.name}}
                .tests__class  {{item.test.group.groupClass}}
                .tests__results {{item.result}}
        router-link(
            data-text='Новый тест'
            replace
            to='/scholar/available-tests' 
        ).new__test Новый тест
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
    created() {
        this.fetchScholarTests(this.user.id),
        this.fetchSubjectsForScholar()
    },
    computed: {
        ...mapState ("user", {
            user: state => state.user}),
        ...mapState ("scholarTests", {
            scholarTests: state => state.scholarTests}),
        ...mapState ("subjects", {
            subjects: state => state.subjects}),
        filteredTests: function() {
            return this.scholarTests
            //filter by subject
            .filter(item => this.selectedSubjectId == 0 || item.test.group.subject.id== this.selectedSubjectId
            )
        }
    }, 
  data () {
    return {
        selectedSubjectId: 0
    }
  },
    methods: {
        ...mapActions("scholarTests", ["fetchScholarTests"]),
        ...mapActions("subjects", ["fetchSubjectsForScholar"])
    }
}
</script>
